<template>
  <div class="quanprogress" :class="{'quan-version2':version2}">
    <p class="p1" @click="openActivityRule">活动规则</p>
    <p class="p2" @click="goExchange"><span>{{activity_promocode.records.length}}</span></p>
    <div>
      <img @click.prevent="" :src="img1" alt="">
      <div class="progress-out">
        <div class="progress-in" :style="styleLong"></div>
      </div>
      <div class="right">还差<span>{{Math.abs(needle)}}</span>元</div>
    </div>
  </div>
</template>

<script>
  import api from './../api/index'
  import CONFIG from './../config/index'
  import {mapState} from 'vuex'

  export default {
    name: 'quanprogress',
    data() {
      return {
        styleLong: '',
        isFirst: true,
        img1:'http://res.catchme.com.cn/activity/jiewu/big/nine2.png',
        img2:'http://res.catchme.com.cn/activity/jiewu/big/long2.png',
        needle:0
    }
    },
    props: ['cinemaInfo'],
    computed: {
      ...mapState({
        activity_promocode: state => state.user.activity_promocode,
      })
    },
    mounted() {
      if (this.version2) {
        this.img1 = this.img2;
      }
      //第一次进来发送一个请求获取用户充值情况
      this.$store.dispatch('getActivityPromocode').then((res) => {
        var bili = res.progress / res.amount - Math.floor(res.progress/res.amount);
//        this.needle = Math.round((1-bili)*res.amount*100)/100;
        this.needle = Math.round((res.amount - (res.progress*100)%(res.amount*100)/100)*100)/100;
        this.styleLong = 'width:' + bili * 100 + '%';
        this.$emit('getVoucherLength',res.records.length)
//        this.styleLong = 'width:'+res.current*100+'%';
      })
    },
    methods: {
      goExchange() {
        if (this.activity_promocode.records && this.activity_promocode.records.length === 0) {
          this.$emit('openTip', 'notExchange');
        } else {
          this.$emit('openTip', 'exchange');
        }
      },
      openActivityRule() {
        this.$emit('openTip', 'activityRule');
      }
    },
    watch: {
      activity_promocode(newActivity, oldActivity) {
        if (this.isFirst) {
          this.isFirst = false;
          return;
        }
        //如果充值成功之后获取到用户的进度没有改变，再过3秒重新请求
        if(newActivity.progress === oldActivity.progress){
          console.log('数据未改变发送请求')
          setTimeout(()=>{
            this.$store.dispatch('getActivityPromocode');
          },3000);
          return;
        }
//        this.styleLong = 'width:'+newActivity.current*100+'%';
        if(newActivity.progress>0 && (((newActivity.progress / newActivity.amount)+"").indexOf('.') === -1)){
          this.styleLong = 'width:100%';
          this.needle = 0;
        }else {
          var bili2 = newActivity.progress / newActivity.amount - Math.floor(newActivity.progress/newActivity.amount);
          this.styleLong = 'width:' + bili2*100 + '%';
//          this.needle = Math.round((1-bili2)*newActivity.amount*100)/100;
          this.needle = Math.round((newActivity.amount - (newActivity.progress*100)%(newActivity.amount*100)/100)*100)/100;
        }
        if (newActivity.records.length > oldActivity.records.length) {
          console.log('弹出提示');
          this.$emit('getVoucherLength',newActivity.records.length)
          setTimeout(()=>{
            this.$emit('openTip', 'exchange2');
            if(newActivity.progress>0 && (((newActivity.progress / newActivity.amount)+"").indexOf('.') === -1)){
            this.styleLong = 'width:0';
            this.needle = newActivity.amount;
            }
          },500);
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  .quanprogress {
    width: 100%;
    height: 100%;
    position: relative;
    padding: 0 22px;
    >div{
      position: relative;
      img{
        width: 100%;
      }
      .right{
        padding: 0 10px;
        height: 58px;
        background: url("./../assets/jiewu/haicbg.png") no-repeat;
        background-size: 100% 100%;
        position: absolute;
        right: 20px;
        bottom: 21px;
        font-size: 20px;
        color: #39f9ff;
        display: flex;
        align-items: center;
        justify-content: center;
        text-shadow: 0 0 5px #f8449f;
        font-weight: 600;
        span{
          font-size: 28px;
        }
      }
    }
    .p1{
      position: absolute;
      padding: 5px 10px;
      /*line-height: 30px;*/
      display: flex;
      align-items: center;
      text-align: center;
      font-size: 20px;
      background: rgba(35,8,85,0.68);
      color: #fff;
      left: 36px;
      top:17px;
      border-radius: 10px;
      z-index: 666;
    }
    .p2{
      width: 200px;
      height:42px;
      position: absolute;
      right: 22px;
      top:0;
      z-index: 2;
      color: #fff;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      font-size: 24px;
      padding: 22px 0 0 0;
     span{
       display: block;
       width: 100px;
       text-align: center;
       &.long{
         font-size: 26px;
       }
     }
    }
  }

  .progress-out {
    width: 640px;
    height: 38px;
    /*background: #491b8f;*/
    background:url("./../assets/small/progressbg.png") no-repeat;
    background-size: 100% 100%;
    /*border: 3px solid red;*/
    border-radius: 19px;
    overflow: hidden;
    position: absolute;
    left: 20px;
    bottom:28px;
    padding:0;
  }

  .progress-in {
    width: 0;
    height: 38px;
    border-radius: 0 19px 19px 0;
    /*background: linear-gradient(to right,#6a2de2,#fb419d);*/
    border-right: 4px solid #39f9ff;
    background: url("http://res.catchme.com.cn/activity/jiewu/big/progress2.png") no-repeat;
    transition: all 0.5s;
    background-size: auto 100%;
  }

  .quan-version2{
    .p1{
      font-size: 22px;
    }
    .p2{
      /*height: 24px;*/
      /*top:20px;*/
      span{
        font-size: 26px;
      }
    }
    >div{
      .right{
        font-size: 22px;
        span{
          font-size: 32px;
        }
      }
    }

  }

</style>



